import React from 'react';
import p from './Post.module.css';

const Post = (props) => {
    return (
        <div className={p.item}>
            <img src="data:image/jpeg;base64," />
            {props.message}
            <div className={p.likes}>{props.likeCount} лайков</div>
        </div>
    );
};

export default Post;    